<template>
  <div class="good-item">
    <div v-for="(item,index) in goods" class="goods" :key="index" @click="jumprouter(index)">
      <img :src="item.show.img" alt="">
      <span class="desc">{{item.title}}</span>
      <span class="price">￥{{item.price}}</span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "GoodItem",
    props:{
      goods: {
        type: Array,
        default() {
          return []
        }
      }
    },
    methods: {
      jumprouter(index) {
        /*console.log(this.goods[index].iid);*/
        this.$router.push('/detail/'+ this.goods[index].iid)
      }
    }
  }
</script>

<style scoped>
  .good-item{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
  }
  .goods{
    width: 46%;
    display: inline-block;
    box-shadow: 0px -1px 1px rgba(100,100,100,0.2);
    padding: 0px 5px 3px 3px;
    font-size: 12px;
    text-align: center;
    border-radius: 3px;
  }
  .goods img{
    width: 100%;
    border-radius: 5px;
  }
  .desc{
    display: block;
    width: 100%;
    height: 20px;
    font-size: 10px;
    text-align: center;
    line-height: 20px;
    overflow: hidden;
  }
  .price{
    color: red;
  }
</style>