<template>
  <div class="home">
    <top-bar-item><div slot="center">购物街</div></top-bar-item>
    <home-item class="fixed" :title="title"
               @clickTab="tabclick" ref="tabcontrol"
               v-show="isshow"></home-item>
    <!--手动轮播图-->
    <!--<home-Swiper :banners="banners"></home-Swiper>-->
    <scroll class="content" ref="scroll" @move="move" @loadmore="loadmore">
      <!--element UI组件轮播图-->
      <swiper-element :banners="banners" @imgOver="imgOver"></swiper-element>
      <!--商品1-->
      <recommend-view :recommends="recommends"></recommend-view>
      <!--商品2-->
      <feature-view/>
      <!--商品控制栏-->
      <home-item :title="title" @clickTab="tabclick" ref="homeTab"></home-item>
      <!--商品展示-->
      <good-item :goods="goods[list1].list"></good-item>
    </scroll>
    <back-top class="top" v-show="topshow" @click.native="returnTop"></back-top>
  </div>
</template>

<script>
  import TopBarItem from 'common/TopBar/TopBarItem'
  import Scroll from 'common/Scroll/Scroll'
  import HomeSwiper from './HomeChildern/HomeSwiper'
  import SwiperElement from './HomeChildern/SwiperElement'
  import RecommendView from './HomeChildern/RecommendView'
  import FeatureView from './HomeChildern/FeatureView'
  import HomeItem from './HomeChildern/HomeItem'
  import GoodItem from './HomeChildern/GoodItem'
  import BackTop from 'content/BackTop/BackTop'

  import {homedata,HomeGoodsdata,HomeGoodstitle} from "network/home";
  export default {
    name: "Home",
    data(){
      return {
        banners: [],
        recommends: [],
        title:[],
        goods: {
          'pop': {page: 0, list: []},
          'new': {page: 0, list: []},
          'sell': {page: 0, list: []}
        },
        list1:'pop',
        homeTop: 0,
        isshow: false,
        topshow: false,
        saveY: 0
      }
    },
    components: {
      TopBarItem,
      HomeSwiper,
      SwiperElement,
      Scroll,
      RecommendView,
      FeatureView,
      HomeItem,
      GoodItem,
      BackTop
    },
    created() {
      this.GetHomedata()
      this.Goodsdata('pop')
      this.Goodsdata('sell')
      this.Goodsdata('new')
      HomeGoodstitle().then(res => {
        res.data.data.filter.list.forEach(n => {
          this.title.push(n.title)
        })
      })
    },
    activated(){
      this.$refs.scroll.scrollTo(0,this.saveY)
      this.$refs.scroll.refresh()
    },
    deactivated() {
      this.saveY = this.$refs.scroll.getScrollY()
    },
    methods: {
      GetHomedata() {
        homedata().then(res => {
         /* console.log(res);*/
          let data = res.data.data
          this.banners = data.banner.list;
          this.recommends = data.recommend.list;
        })
      },
      Goodsdata(type) {
        const page = this.goods[type].page + 1;
        HomeGoodsdata(type, page).then(res => {
          /*console.log(res);*/
          this.goods[type].list.push(...res.data.data.list)
          this.goods[type].page += 1
          this.$refs.scroll.finishPullUp()
          this.$refs.scroll.refresh()
        })
      },
      tabclick(index) {
        switch (index) {
          case 0:
            this.list1 = 'pop';
            break;
          case 1:
            this.list1 = 'new'
            break;
          case 2:
            this.list1 = 'sell'
            break;
        }
        this.$refs.homeTab.current = index
        this.$refs.tabcontrol.current = index
      },
      /*轮播图加载完成后事件*/
      imgOver() {
        /*console.log(this.$refs.homeTab.$el.offsetTop);*/
        this.homeTop = this.$refs.homeTab.$el.offsetTop;
      },
      /*鼠标滑动到选项卡*/
      move(position) {
        this.topshow = (-position.y) > 1000 ? true:false
        this.isshow = (-position.y) >= this.homeTop ? true:false
      },
      /*加载更多数据*/
      loadmore() {
        console.log("上拉加载更多");
        this.Goodsdata(this.list1)
      },
      /*返回到顶部*/
      returnTop() {
        this.$refs.scroll.scrollTo(0,0,300)
      }
    }
  }
</script>

<style scoped>
  .home{
    height: 100vh;
    width: 100%;
    background-color: red;
    position: relative;
  }
  .content{
    width: 100%;
    position: absolute;
    top: 44px;
    bottom: 49px;
    overflow: hidden;
    background-color: white;
  }
  .fixed{
    position: sticky;
    top: 44px;
    z-index: 100;
    background-color: #fff;
  }
  .top{
    position: fixed;
    right: 10px;
    bottom: 49px;
  }
</style>