<template>
  <div>
    <tab-control>
      <tab-control-item class="tab">
        <div v-for="(item,index) in title" :key="index"
             class="tab-control" @click="btnClick(index)" :class="{active:current==index}">
          <span >{{item}}</span>
        </div>
      </tab-control-item>
    </tab-control>
  </div>
</template>

<script>
  import TabControl from 'content/TabControl/TabControl'
  import TabControlItem from 'content/TabControl/TabControlItem'
  export default {
    name: "HomeItem",
    components: {
      TabControl,
      TabControlItem
    },
    props: {
      title: {
        Type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
        current:0
      }
    },
    methods: {
      btnClick(index) {
        this.current = index;
        this.$emit("clickTab",index)
      }
    }
  }
</script>

<style scoped>
  .tab{
    width: 100%;
    display: flex;
    height: 40px;
    line-height: 40px;
  }
  .tab .tab-control{
    flex: 1;
    text-align: center;
  }
  .tab-control span{
    display: inline-block;
    height: 33px;
    width: 40px;
  }
  .tab .active span{
    border-bottom: 2px solid red;
    color: red;
  }
</style>