import {request} from "./request";
export function homedata() {
  return request({
    url: '/home/multidata'
  })
}
export function HomeGoodsdata(type,page) {
  return request({
    url: '/api/h3/home/data',
    params: {
      type,
      page
    }
  })
}
export function HomeGoodstitle() {
  return request({
    url: '/api/h3/home/data',
    params: {
      type: 'pop',
      page: 1
    }
  })
}