<template>
  <div class="swiper">
    <!--轮播图-->
    <div v-for="(item,index) in banners" :key="index" v-show="index===current">
      <a :href="item.link"><img :src="item.image" ></a>
    </div>
    <div class="bar">
      <!--小圆点-->
      <span v-for="(item,index) in banners"
            :class="{active:index===current}"
      @mousemove="mouseOver">

      </span>
    </div>
  </div>
</template>

<script>
  export default {
    name: "HomeSwiper",
    props: {
      banners: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data() {
      return {
        current: 0,
      }
    },
    computed: {
    },
    methods: {
      slideshow() {
        this.current++;
        if(this.current>3) this.current = 0;
      },
      show(){
        setInterval(this.slideshow,1500)
      },
      mouseOver() {
        console.log(this.index);
      }
    },
    created() {
      this.show()
    }
  }
</script>

<style scoped>
  .swiper{
    width: 100%;
    overflow: hidden;
    position: relative;
    top: 44px;
    left: 0;
    right: 0;
  }
  .swiper img{
    width: 100%;
  }
  .bar{
    width: 100%;
    position: absolute;
    bottom: -16px;
    text-align: center;
    height: 40px;
  }
  .bar span{
    width: 10px;
    height: 10px;
    display: inline-block;
    margin-right: 5px;
    border-radius: 50%;
    background-color: #fff;
  }
  .bar .active{
    background-color: rgba(212,62,46,1.0);
  }
</style>